export default {
  name: "header.usercenter",
  comments: {},
  data() {
    return {
      message_notify: undefined,
      show_user_dialog: false,
      item_show: false

    }
  },
  created() {


    document.addEventListener('mouseup', (e) => {
      let tree = this.$refs.el;
      console.log('tree:', tree);
      if (tree) {
        if (!tree.contains(e.target)) {
          this.item_show = true;
          this.$nextTick(() => {
            this.item_show = false;
          });
        }
      }
    });

    this.listener_update_userinfo = this.$hub.on('event:update_userinfo', (data) => {
      this.message_notify = this.$store.state.message_notify;
      console.log('event:update_userinfo:', data, this.message_notify);
      this.$forceUpdate();
    });

    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });
    this.listener_login = this.$hub.on('event:login', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });
  },
  destroyed() {
    this.listener_loginout.off();
    this.listener_login.off();
    this.listener_update_userinfo.off();
  },
  methods: {
    loginout: function () {
      console.log('loginout click');
      this.$store.commit('loginout');
    },
  }
}
